<template>
  <div>
    <div class="create-account-container">
      <BankAccountCreateEdit :docId="0" :name="''" @reloadBankAccountsList="getBanksList" />
    </div>
    <div class="bank-account-list-container">
      <div v-for="k in Object.keys(bankAccountsList)" :key="k" class="bank-account-element" @click="goToTransactions(k)" data-transaction="true">
        <div class="bank-account-title" data-transaction="true">
          {{ bankAccountsList[k].name }}
          <BankAccountCreateEdit :docId="k" :name="bankAccountsList[k].name" @reloadBankAccountsList="getBanksList" />
        </div>
        <div class="bank-account-balance" data-transaction="true">{{ bankAccountsList[k].balance }}â‚¬</div>
      </div>
    </div>
  </div>
</template>

<script>
import BankAccountCreateEdit from "@/components/BankAccountsCreateEdit.vue"
import { mapGetters, mapMutations } from 'vuex';
import { collection, query, getDocs } from "firebase/firestore";

export default {
  name: 'BankAccountsList',
  components: {
    BankAccountCreateEdit,
  },
  data() {
    return {
      bankAccountsList: {},
    }
  },
  computed: {
    ...mapGetters(["db", "activitiesList",]),
  },
  methods: {
    ...mapMutations(["setBankAccountId", "setBankAccountData", "setActivitiesList"]),
    async getBanksList() {
      const q = query(collection(this.db, "bankAccounts"));

      const querySnapshot = await getDocs(q);
      querySnapshot.forEach((doc) => {
        // doc.data() is never undefined for query doc snapshots
        this.bankAccountsList[doc.id] = doc.data();
      });
    },
    goToTransactions(bankAccountId) {
      if(event.target.dataset.transaction == "true") {
        this.setBankAccountId(bankAccountId);
        this.setBankAccountData(this.bankAccountsList[bankAccountId]);
        this.$router.replace({name: "transactions"});
      }
    },
    async getActivitiesList() {
      const activitiesList = [];
      const q = query(collection(this.db, "activities"));

      const querySnapshot = await getDocs(q);
      querySnapshot.forEach((doc) => {
        // doc.data() is never undefined for query doc snapshots
        activitiesList[doc.id] = doc.data();
      });

      this.setActivitiesList(activitiesList);
    },
  },
  mounted() {
    this.getBanksList();
    if(Object.keys(this.activitiesList).length == 0) {
      this.getActivitiesList();
    }
  },
}
</script>

