import { createStore } from 'vuex'

export default createStore({
  state: {
    app: "",
    auth: "",
    db: "",
    uid: "",
    bankAccountId: "",
    bankAccountData: {},
    activitiesList: {},
  },
  getters: {
    app(state) {
      return state.app;
    },
    auth(state) {
      return state.auth;
    },
    db(state) {
      return state.db;
    },
    uid(state) {
      return state.uid;
    },
    bankAccountId(state) {
      return state.bankAccountId;
    },
    bankAccountData(state) {
      return state.bankAccountData;
    },
    activitiesList(state) {
      return state.activitiesList;
    },
  },
  mutations: {
    setApp(state, app) {
      state.app = app;
    },
    setAuth(state, auth) {
      state.auth = auth;
    },
    setDb(state, db) {
      state.db = db;
    },
    setUid(state, uid) {
      state.uid = uid;
    },
    setBankAccountId(state, bankAccountId) {
      state.bankAccountId = bankAccountId;
    },
    setBankAccountData(state, bankAccountData) {
      state.bankAccountData = bankAccountData;
    },
    setActivitiesList(state, activitiesList) {
      state.activitiesList = activitiesList;
    },
  },
  actions: {
  },
  modules: {
  }
})
